@import "./styles/navBar";
@import "./styles/home";
@import "./styles/about";
@import "./styles/skills";
@import "./styles/portfolio";
@import "./styles//contact";
@import "./styles//footer";

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&family=Raleway:ital,wght@1,200;1,500;1,700&display=swap");

#root {
  padding: 0;
  margin: 0;
  max-width: 100vw;
  box-sizing: border-box;
}
body,
body * {
  scroll-behavior: smooth;
  transition: 0.2s all ease-in-out;
  font-family: "Poppins", sans-serif;
  //box-sizing: border-box;
}
body {
  //margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100vw;
  overflow-x: hidden;
  box-sizing: border-box;
}

.pageTitleContainer {
  height: 15rem;
  width: 100%;
  background-image: url("./assists/about_shap_bg.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;

  .pageTitle {
    position: relative;
    font-size: 2.8rem;

    &:before {
      margin: -0.3rem 20%;
      content: "";
      position: absolute;
      bottom: 0;
      width: 60%;
      height: 0.3rem;
      background-color: $color-primary;
    }
  }
}
